  font-family: $trajan-font;
}

.text-serif {
  font-family: $serif-font;
}

.text-serif-bold {
  font-family: $serif-bold-font;
}

.text-sans-light {
  font-family: $sans-serif-light-font;
  -webkit-font-smoothing: initial !important;
  -moz-osx-font-smoothing: initial !important;
}

.text-sans-bold {
  font-family: $sans-serif-bold-font;
}

.text-sans-italic {
  font-family: $sans-serif-italic-font;
}

.text-sans-bold-italic {
  font-family: $sans-serif-bold-italic-font;
}

.serif-font-bold {
  font-family: $serif-bold-font;

  .no-fontface & {
    font-weight: 600;
    font-style: italic;
  }
}

.serif-font {
  font-family: $serif-font;

  .no-fontface & {
    font-style: italic;
  }
}

.sans-serif-font-bold-italic {
  font-family: $sans-serif-bold-italic-font;

  .no-fontface & {
    font-weight: 600;
    font-style: italic;
  }

  & strong {
    font-family: $sans-serif-bold-font;
  }
}

.sans-serif-font-bold {
  font-family: $sans-serif-bold-font;

  .no-fontface & {
    font-weight: 600;
  }
}

.sans-serif-font-italic {
  font-family: $sans-serif-italic-font;

  .no-fontface & {
    font-style: italic;
  }
}

.sans-serif-light-font {
  font-family: $sans-serif-light-font;

  .no-fontface & {
    font-weight: 200;
  }
}

.sans-serif-light-font-italic {
  font-family: $sans-serif-italic-font;

  .no-fontface & {
    font-weight: 200;
  }
}

.sans-serif-font {
  font-family: $sans-serif-font;
}

i,
em {
  @extend .sans-serif-font-italic;

  font-style: normal;
}

b i,
b em,
strong i,
strong em,
em strong,
em b,
i strong,
i b {
  @extend .sans-serif-font-bold-italic;
}

.text-inverted {
  color: $white;
}

a {
  transition: color .15s;
}

.text-white a:not(.btn) {
  text-decoration: underline;
  color: $gray-100;

  &:hover {
    color: darken($gray-100, 15%);
  }
}

// Stories

.story-top {
  h1 {
    margin-bottom: .6rem;
  }

  h2 {
    font-size: 1.25rem;
  }
}

// Page Title
.page-title {
  @extend .sans-serif-light-font;

  border-bottom: 1px solid $gray-200;
  margin-bottom: 40px;
  margin-top: 0;

  h1 {
    margin: 0;
  }

  &,
  * {
    color: $black;
    font-size: 36px;
    line-height: 1.25;

    @include media-breakpoint-up(sm) {
      font-size: 49px;
      line-height: (75em/52);
    }

    @include media-breakpoint-up(md) {
      font-size: 52px;
    }
  }

  .back-to-site-home {
    a {
      color: lighten($black, 5%);
      display: block;
      font-size: 14px;

      span {
        @extend .sans-serif-font-bold;

        color: $brand-primary;
        font-size: 14px;
        line-height: inherit;
      }
    }

    @include media-breakpoint-up(sm) {
      text-align: right;

      a {
        display: inline;
      }
    }

    @include media-breakpoint-up(md) {
      a,
      a span {
        font-size: 18px;
      }
    }
  }
}

