$font-size-base: 1rem;

// SCU Colors
$brand-primary: #b30738;

$link-red: #9e1b32;
$burgundy: #690521;
$maroon: #72001b;

// UI Colors
$brand-success: #058a16;
$brand-info:    #2e6bff;
$brand-warning: #f39237;
$brand-danger:  #d9534f;

// Secondary Colors
$stone: #4e4540;
$navy:  #021d52;
$beige: #dfdbd3;
$black: #000;
$white: #fff;
$cream: #e9e8d5;
$slate: #546575;

$side-navigation-active: $link-red;
$side-navigation-link: #444;

$emergency-color: #f00;

// sass-lint:disable no-color-keywords
$theme-colors: (
  primary:    $brand-primary,
  secondary:  $gray-600,
  success:    $brand-success,
  info:       $brand-info,
  warning:    $brand-warning,
  danger:     $brand-danger,
  light:      $gray-100,
  dark:       $gray-800,
  navy:       $navy,
  stone:      $stone,
  beige:      $beige,
  cream:      $cream,
  slate:      $slate
);
// sass-lint:enable no-color-keywords

// Block
$block-color:              inherit;
$block-bg:                 #fff;
$block-color-inverted:     #fff;
$block-bg-inverted:        #000;
$block-title-font-size-sm: $font-size-base * 4;
$block-title-font-size:    $font-size-base * 3;
$block-lead-font-size-sm:  $font-size-base * 1.5;
$block-lead-font-size:     $font-size-base * 1.3;
$block-vertical-padding:   120px;

// Links
//
// Style anchor elements.
$link-color:            $brand-primary;
$link-decoration:       none;
$link-hover-color:      darken($link-color, 15%);
$link-hover-decoration: underline;

// TODO: Font stack for text utility classes (deprecated)
$serif-font:                  'Bauer Bodoni W01 Italic';
$serif-bold-font:             'BauerBodoniW01-BlackIta';
$sans-serif-font:             'Trade Gothic Next W01';
$sans-serif-light-font:       'Trade Gothic W01 Light';
$sans-serif-bold-font:        'Trade Gothic W01 Bold_2';
$sans-serif-italic-font:      'TradeGothicNextW01-Ital';
$sans-serif-bold-italic-font: 'TradeGothicW01-Bold2Obl';

$trajan-font:                 'Trajan W01 Regular', 'Helvetica Neue', Helvetica, Arial, sans-serif;

// Overrides for Bootstrap
$font-family-sans-serif: $sans-serif-font, -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
$font-family-serif:      $serif-font, Georgia, 'Times New Roman', Times, serif;
$font-family-monospace:  Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;
$font-family-base:       $font-family-sans-serif;
$headings-font-family:   $sans-serif-bold-font;

// Overrides for Select2 Bootstrap 4 theme
$input-border-color:            $gray-400;
$input-focus-border-color:      $brand-primary;
$dropdown-link-active-bg:       $brand-primary;
$dropdown-link-active-color:    $white;
$dropdown-link-disabled-color:  $gray-600;
$dropdown-link-hover-bg:        $brand-primary;
$dropdown-link-hover-color:     $white;
$s2bs-line-height:              1.2;
$input-height:                  2.75rem;

// Pagination overrides

$pagination-color: $brand-primary;
$pagination-active-bg: $brand-primary;
$pagination-hover-color: $maroon;
$pagination-active-border-color: $maroon;

$enable-responsive-font-sizes: true;
