// sass-lint:disable no-duplicate-properties
// sass-lint:disable indentation
// sass-lint:disable no-color-literals
// sass-lint:disable no-vendor-prefixes

// Social brand colors
$twitter: #00aced;
$facebook: #3b5998;
$googleplus: #dd4b39;
$pinterest: #cb2027;
$linkedin: #007bb6;
$youtube: #b00;
$vimeo: #1ab7ea;
$tumblr: #32506d;
$instagram: #517fa4;
$flickr: #ff0084;
$foursquare: #0072b1;
$wordpress: #21759b;
$stumbleupon: #eb4823;
$yahoo: #7b0099;
$rss: #ff7600;
$soundcloud: #ff3a00;

@font-face {
  font-family: 'fontello';
  font-style: normal;
  font-weight: normal;
  src: url('/assets/fonts/fontello/fontello.eot?88632057');
  src: url('/assets/fonts/fontello/fontello.eot?88632057#iefix') format('embedded-opentype'),
    url('/assets/fonts/fontello/fontello.woff?88632057') format('woff'),
    url('/assets/fonts/fontello/fontello.ttf?88632057') format('truetype'),
    url('/assets/fonts/fontello/fontello.svg?88632057#fontello') format('svg');
}

[class^='icon-']::before,
[class*=' icon-']::before {
  display: inline-block;
  font-family: 'fontello';
  font-style: normal;
  font-weight: normal;
  speak: none;
  text-align: center;
  text-decoration: inherit;
  width: 1em;

  // For safety - reset parent styles, that can break glyph codes
  font-variant: normal;
  text-transform: none;

  // fix buttons height, for twitter bootstrap
  line-height: 1em;

  // Font smoothing. That was taken from TWBS
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-flickr::before {
  content: '\e800';
}

.icon-twitter::before {
  content: '\e801';
}

.icon-vimeo::before {
  content: '\e802';
}

.icon-facebook-squared::before {
  content: '\e803';
}

.icon-instagram::before {
  content: '\e804';
}

.icon-pinterest::before {
  content: '\e805';
}

.icon-rss::before {
  content: '\e806';
}

.icon-soundcloud::before {
  content: '\e807';
}

.icon-youtube::before {
  content: '\e808';
}

.icon-linkedin::before {
  content: '\e809';
}

.icon-scu::before {
  content: '\e810';
}

// SCU-specific styles

.social a {
  @include transition(opacity .3s);

  color: $white;
  display: block;
  float: left;
  font-size: 40px;
  height: 64px;
  margin: 0 8px 8px 0;
  opacity: 1;
  text-align: center;
  width: 64px;

  &.facebook {
    background-color: $facebook;
  }

  &.twitter {
    background-color: $twitter;
  }

  &.pinterest {
    background-color: $pinterest;
  }

  &.linkedin {
    background-color: $linkedin;
  }

  &.youtube {
    background-color: $youtube;
  }

  &.vimeo {
    background-color: $vimeo;
  }

  &.soundcloud {
    background-color: $soundcloud;
  }

  &.flickr {
    background-color: $flickr;
  }

  &.rss {
    background-color: $rss;
  }

  &.instagram {
    background: radial-gradient(circle at 33% 100%, #fed373 4%, #f15245 30%, #d92e7f 62%, #9b36b7 85%, #515ecf);
  }

  &:hover {
    opacity: .7;
  }

  &.illuminate {
    background-image: url('/assets/images/icon-illuminate.png');
    background-size: cover;
  }

  &.scu {
    background-color: $brand-primary;
    font-size: 60px;
    line-height: 55px;
  }
}

// sass-lint:enable no-vendor-prefixes
// sass-lint:enable no-duplicate-properties
// sass-lint:enable indentation
// sass-lint:enable no-color-literals
